import { React } from 'react'

import txtImg from '../img/TXT.svg'


const Card = (props) => {

    async function downloadFile(){

        const response = fetch('/descargar/', {
            method: 'POST',
            headers: {'Content-Type': 'application/json'},
            body: JSON.stringify({
                url: props.name
            })
        })
    }

    return (
        <div className = "card pt-3" style = {{width: "14rem"}}>
            <img src = {txtImg} className = "card-img-top" alt="..." width = '100px' height = '200px'/>
            <div className = "card-body justify-content-center">
                <h5 className = "card-title">{props.name.substring(7, props.name.length)}</h5>
                <div className = "d-flex justify-content-center">
                    {/* <button className = "btn btn-primary"
                        onClick = {downloadFile}
                        type = 'submit'
                        downloadFile = 'archivo.txt'
                        >
                        DESCARGAR
                    </button> */}
                    <a className = "btn btn-primary" role = 'button'
                        href = {`https://server-virus.herokuapp.com/${props.name.substring(7, props.name.length)}`} 
                        download
                    >
                        DESCARGAR
                    </a>
                </div>
            </div>
        </div>
    );

}

export default Card;