import { React } from 'react'
import { useQuery } from 'react-query';

import Card from './Card';

// Consulta de obtencion de urls de archvios existentes
async function geturlFiles() {
    const response = await fetch('/geturlFiles/');

    if (!response.ok) {
        throw new Error("Error al obtener la lista de urls")
    }

    return response.json();
}


const Files = () => {

    const query = useQuery('urlFiles', geturlFiles);

    if (query.isLoading) {
        return (
            <div className="spinner-grow text-primary" role="status">
                <span class="visually-hidden">Loading...</span>
            </div>
        )
    }

    if (query.isError) {
        return (
            <div>
                <div className='container-fluid d-flex justify-content-center pt-3'>
                    <div className='row d-flex'>
                        <button className='btn btn-outline-dark btn-lg'
                            onClick={query.refetch}>
                            REFRESCAR
                        </button>
                    </div>
                </div>
                <div className='row d-flex justify-content-center mt-3'>
                    <div className="alert alert-danger" role="alert">
                        Error, parece que en este momento no hay archivos existentes
                    </div>
                </div>
            </div>
        )
    }

    return (
        <div>
            <div className='container-fluid d-flex justify-content-center pt-3'>
                <div className='row d-flex'>
                    <button className='btn btn-outline-dark btn-lg'
                        onClick={query.refetch}>
                        REFRESCAR
                    </button>
                </div>
            </div>
            <div className='row d-flex justify-content-center mt-3'>
                {
                    query.data.Files
                        ? query.data.Files.map(file => <Card name={file} />)
                        : ''
                }
            </div>
        </div>
    );

}

export default Files;